import {
  ChangeEvent,
  useEffect,
  useLayoutEffect,
  useRef,
  useState,
} from "react";
import styles from "./purchase.module.css";
import closeBtn from "../../images/Close.png";
import Done from "../../images/Done.png";
import User from "../../images/User.png";

interface IPurchaseComponent {
  PaymentState: object | any;
  isOpen: boolean;
  setIsOpen: (value: boolean) => void;
}

export const PurchaseComponent = ({
  PaymentState,
  isOpen,
  setIsOpen,
}: IPurchaseComponent) => {
  useEffect(() => {
    document.body.style.overflowY = "hidden";

    return () => {
      document.body.style.overflowY = "scroll";
    };
  }, []);

  const closeModal = () => {
    setIsOpen(false);
  };

  const createLink = async () => {
    // await fetch("https://4v9jz67r-5000.euw.devtunnels.ms/freekassa/callback", {
    //   method: "GET",
    //   mode: "no-cors",
    //   headers: {
    //     "Content-Type": "application/json",
    //   },
    // })
    //   .then((res) => {
    //     if (res.ok) return res.json();
    //   })
    //   .then((data) => {
    //     if (data) return;
    //     console.log("Data: " + data);
    //   })
    //   .catch((error) => {
    //     console.error("Ошибка запроса: " + error);
    //   });

    await fetch("https://region-p.ru/payment", {
      method: "GET",
    })
      .then((response) => {
        response.json();
        console.log(response.json);
      })
      .then((data) => {
        console.log(data);

        // Обработка полученных данных
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const inputRef = useRef<HTMLInputElement>(null);
  const payButton = useRef<HTMLButtonElement>(null);
  const [isDisabledButton, setDisabledButton] = useState<boolean>(false);

  const [isNick, setNick] = useState<string>("");

  useEffect(() => {
    !isNick && setDisabledButton(true);
    console.log(isDisabledButton);
  });

  const validateInput = (e: ChangeEvent<HTMLInputElement>) => {
    let input = inputRef.current;
    let button = payButton.current;

    setNick(e.target.value);
    isNick.length < 8 && setDisabledButton(true);
    isNick.length > 8 && setDisabledButton(false);
  };

  return (
    <div className={styles.backgroundComponent}>
      <div className={styles.backgroundModal}>
        <button
          className={styles.closeBtn}
          onClick={() => {
            closeModal();
          }}
        >
          <img src={closeBtn} alt="Кнопка закрытия модального окна" />
        </button>
        <h1 className={styles.headerConext}>ОПЛАТА</h1>

        <div className={styles.flexContainer}>
          <p className={styles.decriptionSelector}>Выберите сервер:</p>
          <div className={styles.serverSelect}>
            <input type="text" disabled placeholder="Region Poject #1" />
            <img className={styles.iconInput} src={Done} alt="" />
          </div>

          <p className={styles.descriptionNickname}>Введите никнейм:</p>
          <div className={styles.serverSelect}>
            <input
              ref={inputRef}
              value={isNick}
              onChange={validateInput}
              type="nickname"
              placeholder="Nick_Name"
            />
            <img className={styles.iconInput} src={User} alt="" />
          </div>

          <p className={styles.descriptionNickname}>Детали заказа:</p>
          <ul className={styles.detaliesList}>
            <li>
              Название: <span>{PaymentState.name}</span>
            </li>
            <li>
              Описание: <span>{PaymentState.description}</span>
            </li>
            <li>
              К оплате:
              <span>
                {(PaymentState.typeProduct === "money" &&
                  " " + PaymentState.convertPrice + " ") ||
                  " " + PaymentState.price + " "}
                ₽
              </span>
            </li>
          </ul>

          <button
            ref={payButton}
            disabled={isDisabledButton}
            className={styles.payButton}
            onClick={createLink}
          >
            Оплатить
          </button>
        </div>
      </div>
    </div>
  );
};
