import clsx from "clsx";

import styles from "./recover.module.css";
import closeBtnImg from "../../images/Close.png";
import locker from "../../images/123 1.png";
import { useEffect, useRef } from "react";

interface TRecovery {
  isOpen: boolean;
  setIsOpen: (value: boolean) => void;
}

export const Recovery = ({ isOpen, setIsOpen }: TRecovery) => {
  const closeBtn = () => {
    setIsOpen(!isOpen);
  };

  const inputEmail = useRef<HTMLInputElement | null>(null);

  useEffect(() => {
    document.body.style.overflowY = "hidden";
    return () => {
      document.body.style.overflowY = "scroll";
    };
  }, []);

  const sendRecovery = () => {
    const currentEmail = inputEmail.current;

    //в эту переменную будет приходить email
    //console.log(currentEmail?.value);
  };

  return (
    <div className={styles.backgroundRecover}>
      <div className={styles.modalForm}>
        <div className={styles.closeForm}>
          <button className={styles.closeModal} onClick={closeBtn}>
            <img src={closeBtnImg} alt="Кнопка закрытия модального окна" />
          </button>
        </div>
        <h1>Восстановление пароля</h1>

        <div className={styles.modalContent}>
          <div>
            <p>
              <span>Потеря доступа к аккаунту</span> - Серьезная проблема, но не
              стоит переживать, наша команда поможет с восстановлением
              потерянных данных от вашего аккаунта!
            </p>

            <p>Для восстановления доступа достаточно, заполнить форму ниже! </p>
          </div>
          <img src={locker} alt="" />
        </div>

        <div className={styles.line}></div>

        <input
          ref={inputEmail}
          className={styles.emailInput}
          placeholder="Введите email от аккаунта"
          type="text"
        />

        <button onClick={sendRecovery} className={styles.recoverButton}>
          Восстановить
        </button>
      </div>
    </div>
  );
};

