import React, { useState } from "react";

import { Navbar } from "../../Modules/navigation";
import { Footer } from "../../Modules/footer";
import styles from "./shop.module.css";

import "./main.css";

import { initialProducts } from "../../../products/products";
import { ProductCards } from "../../Component/productCard";
import { PurchaseComponent } from "../../Component/purchase";

const ShopPage = () => {
  const [openModal, setOpenModal] = useState<boolean>(false);
  const [getProduct, setProduct] = useState<any | null>(null);
  const [isPaymentOpen, setPaymentOpen] = useState<boolean>(false);
  const [isPaymentList, setPaymentList] = useState<object | null>(null);

  const handlerPaymentList = (value: object | null) => {
    setPaymentList(() => value);
    setPaymentOpen(true);
  };

  const selectProduct = (product: any) => {
    setOpenModal(!openModal);
    setProduct(product);
  };

  return (
    <div>
      <Navbar />
      {openModal && (
        <ProductCards
          isOpen={openModal}
          setIsOpen={setOpenModal}
          isProduct={getProduct}
          isType={getProduct.typeProduct}
          handlerPaymentList={handlerPaymentList}
        />
      )}
      <div className="mainContext">
        <h1 className={styles.headerShop}>
          МОЖНО КУПИТЬ ВСЕ, ЧТО ДУШЕ УГОДНО!
        </h1>
        <div className="containerItems">
          {initialProducts.map((product, index) => (
            <div key={index} className="cardItem">
              <h2 className="header__content">{product.name}</h2>
              <img
                className="image__content"
                src={product.imgaeLin}
                alt="фото товара"
              />
              <div className="containerDo">
                <button onClick={() => selectProduct(initialProducts[index])}>
                  Приобрести
                </button>
                <p>
                  <span className="price__content">{product.price}</span>
                  {product.typeProduct === "item" && <span>₽</span>}
                </p>
              </div>
            </div>
          ))}
        </div>
        {isPaymentOpen && (
          <PurchaseComponent
            isOpen={isPaymentOpen}
            setIsOpen={setPaymentOpen}
            PaymentState={isPaymentList}
          />
        )}
      </div>
      <Footer />
    </div>
  );
};

export default ShopPage;
