import styles from "./productCard.module.css";
import {
  useEffect,
  useLayoutEffect,
  useState,
  ChangeEvent,
  useRef,
} from "react";

import Close from "../../images/Close.png";

interface TProductCard {
  isOpen: boolean;
  setIsOpen: (value: boolean) => void;
  isProduct: any;
  isType: "item" | "money";
  handlerPaymentList: (value: object | null) => void;
}

export const ProductCards = ({
  isOpen,
  setIsOpen,
  isProduct,
  isType,
  //handlePaymentToggle,
  handlerPaymentList,
}: TProductCard) => {
  const [inputValue, setInputValue] = useState<any>("");
  const [convertValue, setConvertValue] = useState<any>("0");
  const [isDisabledButton, setDisabledButton] = useState<boolean>(false);

  const converterFunction = (e: ChangeEvent<HTMLInputElement>) => {
    const validateValue = e.target.value.replace(
      /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[#$@!%&*?])[A-Za-z\d#$@!%&*?]{12,}$/,
      ""
    );
    e.target.value.length < 11 && setInputValue(e.target.value);
    setInputValue(validateValue);
    let currentValue = parseInt(e.target.value);
    currentValue < 10 && setDisabledButton(true);
  };

  useEffect(() => {
    if (isType === "money") {
      let value = parseInt(inputValue);
      !value && setDisabledButton(true);
      value < 10 && setDisabledButton(true);
      value > 9 && setDisabledButton(false);
    }
  });

  useLayoutEffect(() => {
    setConvertValue(inputValue * 2000);
  }, [inputValue]);

  useEffect(() => {
    document.body.style.overflowY = "hidden";

    return () => {
      document.body.style.overflowY = "scroll";
    };
  }, []);

  const handlerPayment = () => {
    let orderProduct = isProduct;
    orderProduct.typeProduct === "money" &&
      (orderProduct.convertPrice = inputValue);
    handlerPaymentList(orderProduct);
    closeModal();
  };

  const closeModal = () => {
    setIsOpen(!isOpen);
  };

  return (
    <div className={styles.backgroundAll}>
      <div className={styles.backgroundModal}>
        <button className={styles.closeButton} onClick={closeModal}>
          <img src={Close} alt="Кнопка закрытия модального окна" />
        </button>
        <div className={styles.contentCard}>
          <div className={styles.imageCard}>
            <img src={isProduct.imgaeLin} alt="Карточка товара" />
          </div>
          <div className={styles.dataCard}>
            <div>
              <h1>{isProduct.name}</h1>
              <p className={styles.description}>{isProduct.description}</p>
            </div>

            <div>
              <div className={styles.transation}>
                <div className={styles.priceCard}>
                  <p>Вы заплатите:</p>
                  {isType === "item" && (
                    <p className={styles.price}>
                      {isProduct.price}
                      {isType === "item" && " ₽"}
                    </p>
                  )}
                  {isType === "money" && (
                    <input
                      className={styles.inputConvert}
                      placeholder="Сумма"
                      type="number"
                      value={inputValue}
                      onChange={converterFunction}
                      maxLength={11}
                    />
                  )}
                </div>
                <div className={styles.priceCard}>
                  <p>Вы получите:</p>

                  <p className={styles.price}>
                    {isType === "item" && isProduct.name}
                    {isType === "money" && convertValue}
                  </p>
                </div>
              </div>

              <button
                disabled={isDisabledButton}
                type="button"
                className={styles.buyBtn}
                onClick={() => handlerPayment()}
              >
                Приобрести
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
