import "./main.css";
import Logo from "../../../images/bely_logo.png";
import "../../assets/fonts/fonts.css";
import { Link, NavLink } from "react-router-dom";
import burger from "../../../images/burgerSVG.svg";

import clsx from "clsx";
import styles from "./menu.module.css";
import { useState } from "react";

import { Recovery } from "../../Component/recover";

export const Navbar = () => {
  const [isStateMenu, setStateMenu] = useState<boolean>(false);
  const [isOpen, setOpen] = useState<boolean>(false);

  const menuBurger = () => {
    setStateMenu(!isStateMenu);
  };

  let menuOpen = clsx([styles.containerMenu], {
    [styles.containerOpen]: isStateMenu,
  });

  const burgerAnimation = clsx({
    [styles.activeButton]: isStateMenu,
  });

  const handleRecovery = () => {
    console.log("Сработал handleRecovery со значением: " + isOpen);
    setOpen(!isOpen);
  };

  return (
    <>
      {isOpen && <Recovery isOpen={isOpen} setIsOpen={setOpen} />}
      <nav className="navBar">
        <button className={burgerAnimation} onClick={menuBurger}>
          <img className="imageMenu" src={burger} alt="кнопка меню сайта" />
        </button>

        <img src={Logo} className="logoSetting" />
        <div className={menuOpen}>
          <ul>
            <li>
              <NavLink to="/">Главная</NavLink>
            </li>
            <li>
              <NavLink to="/start">Как начать играть?</NavLink>
            </li>
            <li>
              <NavLink to="/shop">Магазин</NavLink>
            </li>
            <li>
              <a href="/forum">Форум</a>
            </li>
          </ul>
          <button onClick={handleRecovery} type="button" className="recoverBtn">
            Восстановление пароля
          </button>
        </div>
      </nav>
    </>
  );
};
